@import url("https://fonts.googleapis.com/css2?family=Spartan:wght@100;200;300;400;500;600;700;800;900&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Spartan", sans-serif;
}

html {
  scroll-behavior: smooth;
}

/* Global Styles */

h1 {
  font-size: 50px;
  line-height: 64px;
  color: #222;
}

h2 {
  font-size: 46px;
  line-height: 54px;
  color: #222;
}

h4 {
  font-size: 20px;
  color: #222;
}

h6 {
  font-weight: 700;
  font-size: 12px;
}

p {
  font-size: 16px;
  color: #465b52;
  margin: 15px 0 20px 0;
}

.section-p1 {
  padding: 40px 80px;
}

.section-m1 {
  margin: 40px 0;
}

body {
  width: 100%;
  overflow-x: hidden;
}

.background{
    background-color: rgb(9, 4, 85) !important;
}
.mybtn{
    outline: none;
    border: none;
}

/* Home page */
.main{
    background-image: url("../images/banner.jpg");
    height: 90vh;
    width: 100%;
    background-size: cover;
    background-position: top 25% right 0;
    padding: 0 80px;
    display: flex;
    flex-direction: column;
    align-items: flex-end ;
    justify-content: center;
}
.main h4{
    padding-bottom: 15px;
    font-weight: 600;
    color: #a80973;
}
.main h1{
    color: #a80973;
}
.main p{
    color:rgb(7, 1, 17);
    font-weight: 500;
}
.main .button{
    background-image: url("../images/button.png");
    border-color: transparent;
    color: #e8f0ef;
    border: 0;
    padding: 14px 80px 14px 65px;
    background-repeat: no-repeat;
    cursor: pointer;
    font-weight: 700;
    font-size: 15px; 
    text-decoration: none;
}
 
/* feature box */
.feature{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
}
.feature .fe-box{
    width: 180px;
    text-align: center;
    padding: 25px 15px;
    box-shadow: 20px 20px 34px rgba(0, 0, 0, 0.03);
    border: 1px solid #cce7d0;
    border-radius: 5px;
    margin: 15px 0;
}
.feature .fe-box:hover{
    box-shadow: 20px 20px 34px rgba(5, 5, 219, 0.1);
}
.feature .fe-box img{
     width: 100%;
     margin-bottom: 10px;
}
.feature .fe-box h6{
    display: inline-block;
    padding: 9px 8px 6px 8px;
    line-height: 1;
    border-radius: 5px;
    color: #088178;
    background-color: #f5c3ce;
}
.feature .fe-box:nth-child(1){
    background-color: #c6b3f1;
}
.feature .fe-box:nth-child(2){
    background-color: #ceeebc;
}
.feature .fe-box:nth-child(3){
    background-color: #d1e8f2;
}
.feature .fe-box:nth-child(4){
    background-color: #cdd4f8;
}
.feature .fe-box:nth-child(5){
    background-color: #f6dbf6;
}
.feature .fe-box:nth-child(6){
    background-color: #f8e2cd;
}

/* products-card */
.product1{
    text-align: center;
}
.product1 .pro-container{
    display: flex;
    justify-content: space-between;
    padding-top: 20px;
    flex-wrap: wrap;
}
.product1 .pro{
    width: 23%;
    min-width: 250px;
    padding: 10px 12px;
    border: 1px solid #cce7d0 ;
    border-radius: 25px;
    cursor: pointer;
    box-shadow: 20px 20px 30px rgba(0, 0, 0, 0.02);
    margin: 15px 0;
    transition: 0.2s ease;
    position: relative;
}
.product1 .pro img{
    width: 100%;
    border-radius: 20px;
}
.product1 .pro:hover{
    box-shadow: 20px 20px 30px rgba(0, 0, 0, 0.06);
}
.product1 .pro .des{
    text-align: start;
    padding: 10px 0; 
}
.product1 .pro .des span{
    color: #606063;
    font-size: 12px;
}
.product1 .pro .des h5{
    padding-top: 7px;
    color: #1a1a1a;
    font-size: 14px;
}
.product1 .pro .des i{
    font-size: 12px;
    color: rgb(243, 181,25);
}
.product1 .pro .des h4{
    padding-top: 7px;
    font-size: 15px;
    font-weight: 700;
    color: #088178;
}
.product1 .pro .cart{
    width: 40px;
    height: 40px;
    line-height: 40px;
    border-radius: 50px;
    background-color: #e8f6ea;
    font-weight: 500;
    color: #088178;
    border: 1px solid #cce7d0;
    position: absolute;
    bottom: 20px;
    right: 10px;
}

/* Newslatter */
.newsletter{
    background-image: url("../images/b14.png");
    background-repeat: no-repeat;
    background-position: 20% 30%;
    background-color: #041e42;
}

.newsletter h4{
    font-size: 18px;
    font-weight: 700;
    color:#fff ;
   
    
}
.newsletter p{
    font-size: 14px;
    font-weight: 600;
    color:#818ea0 ;
}
.newsletter p span{
    color:#ffbd27 ;
}

/* footer */
footer{
    display:flex;
    flex-wrap: wrap;

}
footer .col{
    display:flex;
    flex-direction: column;
    align-items: flex-start;
}
footer .logo{
    margin-bottom: 30px;
    height:80px;
    width: 140px;
}
footer h4{
    font-size: 20px;
    color: #222;
    font-size: 14px;
    font-weight: 600;
    padding-bottom: 20px;
    
}
footer p{
    font-size: 13px;
    margin: 0 0 8px 0;
}
footer a{
    font-size: 14px;
    text-decoration: none;
    color: #222;
    margin-bottom: 15px;
}

footer .follow{
    margin-top: 20px;
}

footer .follow h4{
    padding-bottom: 0;
    font-weight: 600;
}
footer .follow .icon{
    margin-top: 5px;
}
footer .follow i{
    font-size:25px; 
    color:#465b52;
    padding-right: 15px;
    cursor: pointer;
}
footer .install .row{
    margin-bottom: 10px;
}
footer .install .row img{
    border: 1px solid #088178;
    border-radius: 6px;
    height: 100%;
    width: 45%;
    margin:0 4px;
}
footer .install img{
    margin:10px 0 15px 0 ;

}
footer .follow i:hover,
footer a:hover{
    color:#088178;
}
footer .copyright{
    width: 100%;
    text-align: center;
}